<template>
  <Loading v-if="loading" v-model:active="loading" :can-cancel="false" :is-full-page="true" color="#4785FE" loader="dots" />
  <div v-else class="bg-white rounded-md p-[12px]">
    <div class="flex items-center justify-between py-[8px] px-[24px]">
      <PageTitle> Markazlar ro`yhati </PageTitle>
      <button class="bg-primary text-white px-[12px] py-[6px] rounded-md">
        <router-link to="/addCenter"> Markaz qo'shish </router-link>
      </button>
    </div>
    <TableCenters :table-body="centersList" @delete="deleteHandler" />
  </div>
  <ModalComponent ref="modalComponentRef" modalClass="!w-1/2" modal-position="center">
    <template #slotBody>
      {{ modalComponentRef.getExternalData().name }}
    </template>
  </ModalComponent>
</template>

<script setup>
import {onMounted, ref} from 'vue';
import axios from '../services/api/axios';
import TableCenters from '../components/Tables/TableCenters';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import ModalComponent from '../components/Common/Modal/ModalComponent';
import PageTitle from '../components/LayoutItems/PageTitle';

const loading = ref(true);
const centersList = ref([]);
const modalComponentRef = ref();
function deleteHandler(data) {
  modalComponentRef.value.setExternalData(data);
  modalComponentRef.value.openModal();
}

// const showModal = () => {
//   modalComponentRef.value.openModal();
// };

onMounted(async () => {
  loading.value = true;
  const {data} = await axios.get('/api/info/markazs/');
  centersList.value = data;
  loading.value = false;
});
</script>

<style scoped></style>
