<template>
  <div v-if="isOpen" class="absolute inset-0 z-10 overflow-hidden bg-gray-800 bg-opacity-50 py-[50px] flex items-center w-full" :class="modalContentPosition" @click="clickAwayHandlder">
    <div class="rounded-md bg-white shadow-sm overflow-y-auto" :class="[modalPosition === 'center' ? 'w-[80%]' : 'w-[40%] h-screen', modalClass]" @click.stop>
      <div class="relative py-10 px-4">
        <span v-if="!hideCloseButton" class="absolute clickEffectBtn right-1 top-1 cursor-pointer text-gray-300" @click="closeModal">
          <svg xmlns="http://www.w3.org/2000/svg" class="h-10 w-10" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z" />
          </svg>
        </span>
        <slot name="slotBody" :closeModal="closeModal" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    modalPosition: {
      type: String,
      default: 'center',
      required: true,
    },
    closeOnClickAway: {
      type: Boolean,
      default: true,
    },
    hideCloseButton: {
      type: Boolean,
      default: false,
    },
    modalClass: {
      type: String,
    },
  },
  data() {
    return {
      isOpen: false,
      modalContentPosition: {
        'justify-center': this.modalPosition === 'center',
        'justify-end': this.modalPosition === 'right',
        'justify-start': this.modalPosition === 'left',
        // 'px-[300px]': this.modalPosition === 'center',
      },
      modalContentMinWidth: {},
      externalData: undefined,
    };
  },
  computed: {
    clickAwayHandlder() {
      return this.$props.closeOnClickAway ? this.closeModal : null;
    },
  },
  methods: {
    setExternalData(data) {
      this.externalData = data;
    },
    getExternalData() {
      return this.externalData;
    },
    closeModal() {
      this.isOpen = false;
    },
    openModal() {
      this.isOpen = true;
    },
  },
};
</script>

